<template>
  <div class="resume-appraise-page">
    <v-row dark>
      <v-card dark width="45%" style="margin-left:25%;margin-top:5%">
        <v-toolbar flat color="#64DD17" dark>
          <v-toolbar-title>自我评价</v-toolbar-title>
        </v-toolbar>

        <v-card-text>
          <v-row>
            <v-text-field filled label="" :value="introduction.workingFor"></v-text-field>
          </v-row>
          <v-textarea auto-grow dark shaped outlined label="工作上" :rows="introduction.work.length" :value="introduction.work | cookWords"></v-textarea>
          <v-textarea auto-grow dark shaped outlined label="生活上" :rows="introduction.life.length" :value="introduction.life | cookWords"></v-textarea>
        </v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="#76FF03" outlined class="mx-10">
            <v-icon color="#FF5722">fa-heart</v-icon>点赞~
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-row>
  </div>
</template>

<script>
  import CONFIG from '@/config/personalInfo.js'
  export default {
    data () {
      return {
        introduction: CONFIG.introduction,
      }
    },
    filters: { 
      cookWords(value){
        let str = "";
        if(value && (value instanceof Array)){
          for(let v of value){
            str += "    ●    " + v + "\n"
          }
        }
        return str.substr(0, str.length - 1)
      },
    },
  }
</script>

<style lang="stylus" scoped>
.resume-appraise-page
  top  0
  left 0
  right 0
  bottom 0
  width 100%
  height  100%
  position  relative
  background-size cover
  background linear-gradient(rgb(69, 69, 70) 0%, rgb(0, 0, 0) 100%)

.contact-card
  margin-top 10%
  margin-left 16%

.mx-20
  margin-left 20px

.mx-10
  margin-left 10px
</style>